.transactionhistory {
    width: 400px;
    margin-left: auto;
    margin-right: auto;
    border-collapse: collapse;
    border: 3px solid grey;
    box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);
  }
  
  .transactionHistory th {
    width: 33%;
    border: 1px solid grey;
    background-color: rgb(85, 177, 187);
    color: aqua;
    padding: 10px;
  }
  .transactionHistory td {
    font-size: small;
    padding: 5px;
    text-align: center;
    border: 1px solid grey;
  }
  
  tr:nth-child(2n) {
    background-color: rgba(79, 94, 93, 0.801);
  }