.statistics {
    width: 400px;
    margin-right: auto;
    margin-left: auto;
    margin-bottom: 100px;
    box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);
    padding-top: 15px;
  }
  
  .title {
    text-align: center;
  }
  
  .statList {
    display: flex;
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .item {
    width: 25%;
    display: flex;
    flex-direction: column;
    text-align: center;
  }
  
  .label,
  .percentage {
    margin-bottom: 10px;
  }